import Header from "./Header";
import React, { useState, useEffect } from "react";
import "./App.css";
import Sidebar from "./Sidebar";
import Minisidebar from "./Minisidebar";
import { BrowserRouter as Router, NavLink, Route } from "react-router-dom";
import Main from "./Main";
import Searchcard from "./Searchcard";
import trending from "./json/trending.json";
import music from "./json/music.json";
import movies from "./json/movies.json";
import gaming from "./json/gaming.json";
import Popup from "./Popup";

function App() {
  const [minibar, setMinibar] = useState("0");
  const [largeMedia, setLargemedia] = useState(false);
  const [popUp, setPopup] = useState(false);
  const [smallMedia, setSmallmedia] = useState(false);
  useEffect(() => {
    const mediaWatcher = window.matchMedia("(min-width: 840px)");
    setLargemedia(mediaWatcher.matches);
    mediaWatcher.addEventListener("change", (e) => {
      setLargemedia(e.matches);
      if (largeMedia === false) {
        setMinibar("1");
      }
    });
    const mediaWatcher1 = window.matchMedia("(max-width: 640px)");
    setSmallmedia(mediaWatcher1.matches);
    mediaWatcher1.addEventListener("change", (e) => {
      setSmallmedia(e.matches);
    });
    return () => {
      mediaWatcher.removeEventListener("change", (e) => {
        setLargemedia(e.matches);
      });
    };
  }, []);

  const sidebar = () => {
    if (minibar === "0" && largeMedia === true) {
      setMinibar("1");
    } else if (minibar === "1" && largeMedia === true) {
      setMinibar("0");
    } else if (smallMedia === true) {
      setPopup(true);
    }
  };
  function popup() {
    setPopup(false);
  }

  return (
    <div>
      <Header sidebar={sidebar} />
      <div className="app">
        {minibar === "1" ? (
          <div>
            <Minisidebar sidebar={sidebar} />
          </div>
        ) : (
          <div>
            <Sidebar sidebar={sidebar} />
          </div>
        )}
        {popUp ? <Popup popup={popup} /> : <div></div>}

        <Route exact path="/">
          <Main />
        </Route>
        <Route path="/trending">
          <div className="flex">
            {trending.map((home) => (
              <Searchcard
                url={home.src}
                title={home.title}
                logo={home.logo}
                channel={home.channel}
                view={home.views}
                time={home.date}
              />
            ))}
          </div>
        </Route>
        <Route path="/music">
          <div className="flex">
            {music.map((home) => (
              <Searchcard
                url={home.src}
                title={home.title}
                logo={home.logo}
                channel={home.channel}
                view={home.views}
                time={home.date}
              />
            ))}
          </div>
        </Route>
        <Route path="/movies">
          <div className="flex">
            {movies.map((home) => (
              <Searchcard
                url={home.src}
                title={home.title}
                logo={home.logo}
                channel={home.channel}
                view={home.views}
                time={home.date}
              />
            ))}
          </div>
        </Route>
        <Route path="/gaming">
          <div className="flex">
            {gaming.map((home) => (
              <Searchcard
                url={home.src}
                title={home.title}
                logo={home.logo}
                channel={home.channel}
                view={home.views}
                time={home.date}
              />
            ))}
          </div>
        </Route>
      </div>
    </div>
  );
}

export default App;
